.containerr
{
	display: grid;
	grid-template-columns: 2fr 1fr;
	margin-top: 200px;

}

.f
{
	text-align: left;
}

.box
{
	padding-left: 100px;
}

.gold
{
	color: #f9bb48;
}

.f
{
	font-family: 'Fjalla One', sans-serif;
}

button
{
	background-color: #f9bb48;
	padding: 10px 50px 10px 50px;
	border-radius: 40px;
	border: none;
	color: white;
	cursor: pointer;
	transition: all .3s;
}

button:hover
{
	background-color: #FFA909; 
}

img
{
	height: 500px;
	width: auto;
}

.bars
{
	text-align: center;
}

.a,.b
{
  cursor: pointer;
  height: 5px;
  width: 55px;
  margin: 0 10px;
  border: 2px solid #FFA909;
  border-radius: 10px;
  display: inline-block;
  transition: background-color 0.6s ease;
}

.a{
	background-color: #FFA909;
}