/*@import url('https://fonts.googleapis.com/css2?family=Titillium+Web&display=swap" rel="stylesheet')*/

.container
{
	display: grid;
	grid-template-columns: 1fr 1fr;
	position:fixed;
	top: 0;
	background-color: white;
}

.logo
{
	width: 30%;
	height:auto;
	padding:2%;
	padding-top: 4%;
}

.asd
{
	padding: 15px;
	margin: 2px;
	margin-top: 20px;
	cursor: pointer;
	transition: all .1s;
	color:#999999;
}

.asd:hover
{
	border-bottom: 5px solid #f9bb48;
	color: black;
}

.main-nav
{
	display: flex;
	list-style: none;
	margin: 0;
}

#s
{
	border-bottom: 5px solid #f9bb48;
	color: black;
}
