.i
{
	height: 50px;
	width: auto;
}

.cardd
{
	text-align: center;
	box-shadow: 0 8px 30px 0 rgba(0,0,0,0.2);
	margin: 35px;
	padding: 10px;
	border-radius: 20px;
	transition: all .3s ease-in;
	cursor: pointer;
}

.cardd:hover
{
	 transform: rotate(10deg);
	 box-shadow: 0 20px 50px 0 rgba(0,0,0,0.2);
}

.cookies
{
	display: grid;
	grid-template-columns: 1fr 1fr;
	/*margin: 15px*/
}

.bananas
{
	padding: 45px;
}

.fruits
{
	display: grid;
	grid-template-columns: 1fr 1fr;
}

.n
{
	font-size: 30px;
	font-weight: bold;
}

.m
{
	font-size: 20px;
	font-weight:bold;
}

.v
{
	color: #999999;
}