.plm
{
	display: grid;
	grid-template-columns: 1fr 1fr;
	margin-top: 100px;
}

.h
{
	text-align: left;
}

.iu
{
	padding: 50px;
}

.ui
{
	padding: 50px;
	/*margin: 50px;*/
	width: 400px;
	height: auto;
}

.rt
{
	border:2px solid #f9bb48;
	color: #f9bb48;
	border-radius: 20px;
	padding: 5px;
	height: 1.5em;
	margin: 10px;
	transition: all .3s;
	box-shadow: rgba(0, 0, 0, 0.2) 0px 4px 8px 0px, rgba(0, 0, 0, 0.19) 0px 6px 20px 0px;
}

.tr
{
	width: 100%;
	height: 200px;

}

.rt:hover
{
	background-color:#f9bb48;
	color: white; 
}

.g
{
	color: #999999;
}