.write
{
	height: 50px;
	width: auto;
}

.qw
{
	color:#999999;
}

.compartment
{
	display: grid;
	grid-template-columns: 1fr 1fr;
	margin-top: 150px;
}

.options
{
	display: grid;
	grid-template-columns: 1fr 1fr;
	/*padding: 100px;*/
}

.text
{
	padding: 50px;
}

.set
{
	padding: 10px;
	text-align: center;
	transition: all .5s;
	cursor: pointer;
}

.set:hover
{
	border: 3px dotted #f9bb48;
	border-radius: 10%;
	/*transform: scale(1.1);*/
	box-shadow: 0 8px 30px 0 rgba(0,0,0,0.2);
	/*background-color: #999999;*/
}

.grey
{
	color:#999999;
}